
.find-dentists-no-dentist {
  position: relative;
  width: 100%;
  min-height: 300px;
  text-align: center;
  font-size: 16px;
}
.find-dentists-no-dentist-message {
  height: 30px;
  position: relative;
  top: 110px;
}
.find-dentists-wait-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16px;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: rgba(255, 255, 255, 0.9);
}
.find-dentists-wait-message {
  height: 30px;
  line-height: 30px;
  position: relative;
  font-size: 16px;
}
.spin {
  height: 45px;
  width: 45px;
  border-radius: 45px;
  border-top: 3px solid #3286b0;
  border-right: 3px solid #3286b0;
  border-bottom: 3px solid #ffffff;
  border-left: transparent;
  -webkit-animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
          animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
  position: relative;
}
@-webkit-keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.find-dentists-result {
  width: 95%;
  border-bottom: 1px solid #a7a7a7;
  min-height: 180px;
  padding: 10px 0px;
}
.find-dentists-gravatar-section {
  width: 60px;
  height: 60px;
  float: left;
  display: inline-block;
  margin-right: 10px;
  border: 1px solid #c3c3c3;
  border-radius: 6px;
  padding: 3px;
}
.find-dentists-gravatar-section > canvas {
  height: 100%;
  width: 100%;
  border-radius: 6px;
}
.find-dentist-about-section {
  width: 250px;
  height: 150px;
  display: inline-block;
  float: left;
}
.find-dentist-about-section > div {
  display: block;
  height: 20px !important;
  line-height: 20px !important;
  font-size: 14px;
  text-align: left;
  width: 100%;
}
.profile-link {
  font-size: 10px !important;
  color: #bfced9;
  cursor: pointer;
}
.find-dentists-average-rating {
  width: 100% !important;
}
.find-dentists-average-rating > div {
  background: url(/static/images/star_line.png) no-repeat;
  background-size: contain;
  height: 20px;
  width: 20px;
  display: inline-block;
  float: left;
}
.find-dentists-average-rating > div.find-dentists-only-patient {
  cursor: pointer;
}
.find-dentists-average-rating:hover > div.find-dentists-only-patient {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.find-dentists-average-rating > div.find-dentists-only-patient:hover {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.find-dentists-average-rating > div.find-dentists-only-patient:hover ~ div.find-dentists-only-patient {
  background: url(/static/images/star_line.png) no-repeat;
  background-size: contain;
}
.find-dentists-average-rating > .filled {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.find-dentists-request-appointment-section {
  width: auto;
  height: 150px;
  line-height: 150px;
  display: inline-block;
  float: right;
}
.find-dentists-link-to-appointment {
  width: 200px;
  height: 40px;
  line-height: 40px;
  color: #ffffff;
  background: #3285b1 !important;
  display: inline-block;
  text-decoration: none;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
}



