
.no-application {
  position: absolute;
  top: 160px;
  width: 100%;
  min-height: 260px;
  text-align: center;
  font-size: 16px;
}
.no-application-message {
  height: 30px;
  position: relative;
  top: 110px;
}
.applications-wait-overlay {
  position: absolute;
  top: 160px;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16px;
  height: 260px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: rgba(255, 255, 255, 0.9);
}
.applications-wait-message {
  height: 30px;
  line-height: 30px;
  position: relative;
  font-size: 16px;
}
.spin {
  height: 45px;
  width: 45px;
  border-radius: 45px;
  border-top: 3px solid #3286b0;
  border-right: 3px solid #3286b0;
  border-bottom: 3px solid #ffffff;
  border-left: transparent;
  -webkit-animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
          animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
  position: relative;
}
@-webkit-keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.applications-result {
  width: 95%;
  border-bottom: 1px solid #a7a7a7;
  min-height: 180px;
  padding: 10px 0px;
  display: block;
  float: left;
}
.applications-gravatar-section {
  width: 60px;
  height: 60px;
  float: left;
  display: inline-block;
  margin-right: 10px;
  border: 1px solid #c3c3c3;
  border-radius: 6px;
  padding: 3px;
}
.applications-gravatar-section > canvas {
  height: 100%;
  width: 100%;
  border-radius: 6px;
}
.applications-about-section {
  width: 80%;
  display: inline-block;
  float: left;
}
.applications-about-section > div {
  display: block;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  text-align: left;
  width: 100%;
  font-weight: bold;
}
.applications-about-section > div span {
  font-weight: normal;
}
.applications-about-section > .applications-comment {
  height: auto;
}
.applications-profile-link {
  font-size: 10px !important;
  color: #bfced9;
  cursor: pointer;
}
.applications-service-name {
  width: 50%;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
}
.applications-service-name span {
  color: #115588;
}
.applications-quote {
  width: 50%;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
}
.applications-average-rating {
  width: 100% !important;
  margin: 20px 0px;
}
.applications-average-rating > div {
  background: url(/static/images/star_line.png) no-repeat;
  background-size: contain;
  height: 20px;
  width: 20px;
  display: inline-block;
  float: left;
}
.applications-average-rating > div.applications-only-patient {
  cursor: pointer;
}
.applications-average-rating:hover > div.applications-only-patient {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.applications-average-rating > div.applications-only-patient:hover {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.applications-average-rating > div.applications-only-patient:hover ~ div.applications-only-patient {
  background: url(/static/images/star_line.png) no-repeat;
  background-size: contain;
}
.applications-average-rating > .filled {
  background: url(/static/images/star.png) no-repeat;
  background-size: contain;
}
.applications-button {
  padding: 0px 2px;
  text-align: center;
  outline: 0px;
  border: 0px;
  cursor: pointer;
  height: 30px;
  line-height: 30px;
  width: 100px;
  background: #29aae1;
  color: #ffffff;
  /*margin-left: 10px;*/
  display: inline-block;
}
.accept-application {
  background: #3285b1 !important;
  text-decoration: none;
  margin: 20px 10px 0px 0px !important;
}



