
.grouping {
  width: 100%;
  min-height: 520px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #edefef;
}
.appointment-entry-item {
  height: 60px;
  margin-top: 30px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 50%;
}
.appointment-entry-param {
  color: #7f7f7f;
  margin-bottom: 5px;
  height: 20px;
  font-size: 14px;
  line-height: 20px;
}
.appointment-list, label.appointment-list {
  display: block;
  height: 30px;
  width: 100%;
  background: #ffffff;
  outline: none;
  border: 1px solid #d3d3d3;
  color: #7f7f7f;
}
.spin {
  height: 45px;
  width: 45px;
  border-radius: 45px;
  border-top: 3px solid #3286b0;
  border-right: 3px solid #3286b0;
  border-bottom: 3px solid #ffffff;
  border-left: transparent;
  -webkit-animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
          animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
  position: relative;
}
@-webkit-keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.appointment-button {
  padding: 2px;
  text-align: center;
  outline: 0px;
  border: 0px;
  cursor: pointer;
  height: 30px;
  line-height: 30px;
  width: 100px;
  background: #29aae1;
  color: #ffffff;
  margin-left: 10px;
  display: inline-block;
}
input#appointment-scan-date, input#appointment-treatment-date {
  height: 28px !important;
  width: 100% !important;
  background: #ffffff !important;
  outline: none !important;
  border: 0px;
  color: #7f7f7f !important;
  cursor: pointer;
}
.insurance-address {
  height: 70px !important;
  max-width: 100%;
}
.tip {
  color: red;
}



