
.search-page-search-item {
  width: 80%;
  height: 80px;
  margin-bottom: 25px;
  line-height: 25px;
}
.search-page-search-param {
  color: #7a7a7a;
  margin-bottom: 15px;
  height: 20px;
  font-size: 16px;
  line-height: 20px;
}
.search-page-list {
  height: 30px;
  width: 200px;
  color: #7a7a7a;
  background: #ffffff;
  outline: none;
  border: 1px solid #d3d3d3;
}
.error {
  color: #f18787;
}
.query-item {
  display: inline-block;
  background: #29aae3;
  color: #ffffff;
  height: 30px;
  line-height: 30px;
  float: left;
  margin-right: 10px;
  padding: 0px 10px;
}



