
.services-no-service {
  position: absolute;
  top: 40px;
  width: 100%;
  min-height: 260px;
  text-align: center;
  font-size: 16px;
}
.services-no-service-message {
  height: 30px;
  position: relative;
  top: 110px;
}
.services-wait-overlay {
  position: absolute;
  top: 40px;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16px;
  height: 260px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: rgba(255, 255, 255, 0.9);
}
.services-wait-message {
  height: 30px;
  line-height: 30px;
  position: relative;
  font-size: 16px;
}
.spin {
  height: 45px;
  width: 45px;
  border-radius: 45px;
  border-top: 3px solid #3286b0;
  border-right: 3px solid #3286b0;
  border-bottom: 3px solid #ffffff;
  border-left: transparent;
  -webkit-animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
          animation: odll-spin 1.2s cubic-bezier(0.2, 0.92, 0.94, 0.9) infinite;
  position: relative;
}
@-webkit-keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes odll-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.services-result {
  width: 95%;
  border-bottom: 1px solid #a7a7a7;
  min-height: 180px;
  padding: 10px 0px;
}
.services-service-name {
