  line-height: 30px;
  width: 100px;
  background: #29aae1;
  color: #ffffff;
  margin-left: 10px;
  display: inline-block;
}
.services-edit-service, .services-delete-service {
  background: #3285b1 !important;
  text-decoration: none;
  margin: 0px 10px 0px 0px !important;
}



